<template>
  <div class="banner">
		<ul class="clearfix">
	 		<router-link
			  v-for="item of data"
			  :key="item._id"
			  :to="{name:'detail',params:{_id:item._id},query:{apiName:apiName}}"
			  tag="li"
			>
				<img :src="$baseUrl + item.banner" alt=""/>
				  <div class="text-box">
				    <h2>{{item.title}}</h2>
				    <p>{{item.sub_title}}</p>
				  </div>
			</router-link>
		</ul>
  </div>
</template>

<script>
	import Swipe from '../assets/js/swipe.js'
	import $ from '../assets/js/jquery-3.4.1.js'
  export default {
    name:'Swiper',
    props:['data','apiName'],
    data(){
      return {
        
      }
    },
    components:{},
    mounted(){
		console.log('swiper mounted')	
	},
    updated(){
		console.log('swiper updated',this.data)
		new Swipe($('.banner')[0],{
		  auto:2000,
		  continuous:true,
		  stopPropation:true,
		  callback:function (index,element){
			$('.banner ol li').removeClass('active');
			$('.banner ol li').eq(index).addClass('active');
			  }
			})
	},
    methods:{}
  }
</script>

<style scoped>
